import React, { useEffect, useState } from 'react'
import { useNavigate } from 'react-router-dom'
import { CAvatar, CDropdown, CDropdownItem, CDropdownMenu, CDropdownToggle } from '@coreui/react'
import { cilLockLocked } from '@coreui/icons'
import CIcon from '@coreui/icons-react'
import avatar8 from '../../assets/images/avatars/default-profile.png'
import { ImageUrl, getDecodeData, getToken } from '../../constant/Global'
import useAxios from 'src/constant/UseAxios'

const AppHeaderDropdown = (profilePic) => {
  let api = useAxios()
  const navigate = useNavigate()
  const user = getDecodeData()
  const Token = getToken()
  const tokenlogout = Token.Token
  const [randomImage, setRandomImage] = useState('')
  const logout = localStorage.getItem('logout')
  const handleLogout = async () => {
    await api
      .post(
        `auth/signout?logout=${logout}&token=${tokenlogout}&refreshToken=${Token.RefreshToken}`,
        {},
      )
      .then((response) => {
        window.localStorage.clear()
        localStorage.removeItem('userData')
        localStorage.removeItem('logout')
        localStorage.removeItem('Pm_tool')
        localStorage.clear()
        window.location.href = response.data
      })
  }
  useEffect(() => {
    if (!user) {
      navigate('/')
    }
  })
  return (
    <CDropdown variant="nav-item">
      <CDropdownToggle placement="bottom-center" className="py-0 " caret={false}>
        <CAvatar
          src={
            profilePic.profilePic === 'default-profile.png'
              ? avatar8
              : profilePic.profilePic
              ? ImageUrl + profilePic.profilePic
              : avatar8
          }
          style={{
            cursor: 'pointer',
            width: '43px',
            height: '46px',
            marginRight: '10px',
            marginTop: '16px',
          }}
          size="md"
        />
      </CDropdownToggle>
      {/* <CDropdownMenu className="pt-0" placement="bottom-end">
        <CDropdownItem
          className="text-c my-1 text-center"
          onClick={handleLogout}
          style={{ cursor: 'pointer', fontSize: '14px' }}
        >
          <CIcon icon={cilLockLocked} className="me-2" />
          Log Out
        </CDropdownItem>
      </CDropdownMenu> */}
    </CDropdown>
  )
}

export default AppHeaderDropdown
